import './dashboard.css';

import { Badge } from 'primereact/badge';
import { Button } from 'primereact/button';
import { Menu } from 'primereact/menu';
import { MenuItem, MenuItemOptions } from 'primereact/menuitem';
import React, { useRef } from 'react';

import { useAuth } from '../../hooks/use-auth';

export const AccountButton = () => {
  const { logout } = useAuth();
  const menu = useRef(null);
  const items = [
    {
      label: 'Profile',
      icon: 'pi pi-user',
    },
    {
      label: 'Invitations',
      icon: 'pi pi-envelope',
      template: (item: MenuItem, options: MenuItemOptions) => {
        return (
          <a className={options.className} target={item.target} onClick={options.onClick}>
            <span className={options.iconClassName + ' pi ' + item.icon}></span>
            <span className={options.labelClassName}>{item.label}</span>
            <Badge value="2" severity="info" className="ml-2"></Badge>
          </a>
        );
      },
    },
    {
      separator: true,
    },
    {
      label: 'About',
      icon: 'pi pi-info-circle',
    },
    {
      separator: true,
    },
    {
      label: 'Logout',
      icon: 'pi pi-external-link',
      command: logout,
    },
  ];

  const onAccountClick = (event: any) => {
    // @ts-ignore
    menu.current.toggle(event);
  };

  return (
    <>
      <Menu model={items} popup ref={menu} id="popup_menu" />
      <Button
        icon="pi pi-user"
        className="p-button-rounded p-button-secondary p-button-outlined"
        aria-label="User"
        onClick={onAccountClick}
        aria-controls="popup_menu"
        aria-haspopup
      />
      <Badge value="2" severity="info" className="account-badge"></Badge>
    </>
  );
};
