import { useState } from 'react';

type Lines = Record<string, { x1: number; y1: number; x2: number; y2: number }>;

export interface Background {
  lines: Lines;
  drawLine: (id: string, x1: number, y1: number, x2: number, y2: number) => void;
  undrawLine: (...id: string[]) => void;
}

export const useMindmapBackground = (): Background => {
  const [lines, setLines] = useState<Lines>({});

  const drawLine = (id: string, x1: number, y1: number, x2: number, y2: number) => {
    setLines((lines) => ({ ...lines, [id]: { x1, y1, x2, y2 } }));
  };

  const undrawLine = (...ids: string[]) => {
    setLines((lines) => {
      ids.forEach((id) => {
        delete lines[id];
      });
      return lines;
    });
  };

  return { drawLine, undrawLine, lines };
};
