import { uuidv4 } from '@firebase/util';
import structuredClone from '@ungap/structured-clone';

import { Tree } from '../pages/mindmap/types';
import { treeToFlat } from './treeToFlat';

/**
 * Converts trees to an array of flat nodes and regenerates each node's
 * id while keeping the hierarchical structure by adjusting the PIDs.
 * @param children The three's themself.
 * @param idMap An object with the node's old id as key and new id as value. (Could be used to set the PID of the 'root' node manually.)
 * @returns A FlatNode array with newly generated ids.
 */
export const copyTreeToNodes = (
  children: Tree[] | undefined,
  idMap: Record<string, string>,
  { keepIds = false, ignoreIds = [] }: { keepIds: boolean; ignoreIds?: string[] },
) => {
  let flatNodes = [];
  // don't modify children from call
  children = structuredClone(children);

  if (children) {
    let child: Tree | undefined;
    while ((child = children.shift())) {
      if (!ignoreIds.includes(child.id)) {
        let newId = keepIds ? child.id : uuidv4();
        idMap[child.id] = newId;
        flatNodes.push(treeToFlat({ ...child, id: newId, pid: child.pid ? idMap[child.pid] : null }));

        if (child.children) {
          children.push(...child.children);
        }
      }
    }
  }

  return flatNodes;
};
